/**
 * This service file handles calling of services, and making store mutations with their responses.
 * I honestly don't know where best to put these kinda actions, so this is a stop gap solution begging
 * for refactoring from a smarter mind, or future me :)
 *
 */

import * as ServiceRequestService from "./ServiceRequestService";
import * as mutationTypes from "../store/mutation-types";
import store from "../store";

/**
 * Get and save, applications and agents belonging to the stockbroker company to the store
 *
 * @param stockbroker_id
 */
export const SaveServiceRequestsToStore = issuer_id => {
  ServiceRequestService.GetServiceRequests(issuer_id)
    .then(response => {
      const serviceRequests = response.data.data;
      store.commit(
        mutationTypes.SAVE_SERVICE_REQUESTS_TO_STORE,
        serviceRequests
      );
    })
    .catch(error => {
      console.log(error);
    });
};



