<template>
  <div>
    <v-dialog v-model="showInitiateAllocationDialog" lazy absolute persistent width="40%">
        <!--Button used to trigger the opening of the edit user dialog -->
        <v-btn id="openInitiateAllocationDialog"
        style="display: none" color="primary" dark slot="activator" ref="triggerButton">
        </v-btn>

        <v-card>
          <v-card-title class="d-flex justify-center bg-csp-light-blue p5 pt10">
              <h5 class="white--text font-size-20px">INITIATE ALLOCATION</h5>
          </v-card-title>
            <!-- Content -->
          <v-card-text>
              <v-layout>
              <v-flex>
                  
                  Are you sure you want to begin the allocation process for the IPO.
                  <!-- Kindly note that rejecting an application is an irreversible action.<br>
              <b class="mt10">Do you agree to reject the application?</b> -->
              </v-flex>
              </v-layout>

                <!-- Loading Icon when approving application-->
                <v-layout v-if="isInitiatingAllocation"  row class=" d-flex justify-center ">
                  <v-flex class="xs6 d-flex justify-center align-center">
                    <v-progress-circular indeterminate class="primary--text height-20px width-20px"></v-progress-circular>
                    <span class="font-size-12 ml5">Initiating allocation process...</span>
                  </v-flex>
                </v-layout>

              <!-- Buttons -->
              <v-container fluid class="p0 ">
                  <v-layout  row class="mt20">
                      <v-flex xs12 lg6>
                      <v-btn small block info class=" darken-1 white--text"
                      @click="initiateAllocation()"
                      :disabled="isInitiatingAllocation" :loading="isInitiatingAllocation">
                      INITIATE  ALLOCATION
                      </v-btn>
                  </v-flex>
                  <v-flex xs12 lg6>
                      <v-btn small block class="red darken-1 white--text"
                      @click="closeInitiateAllocationDialog()">
                      CANCEL
                      </v-btn>
                  </v-flex>
                  </v-layout>
              </v-container>
          </v-card-text>
        </v-card>
    </v-dialog>

      <!-- Snackbar to show success message when application is rejected -->
      <v-snackbar success
        :timeout="snackbarTimeout"
        :top="true"
        :color="'success'"
        v-model="allocationInitiated"
        >
        Allocation initiated
      </v-snackbar>

      <!-- Snackbar to show error message when application rejection fails -->
      <v-snackbar error
        :timeout="snackbarTimeout"
        :top="true"
        :color="'error'"
        v-model="allocationInitiationFailed"
        >
        Failed to initiate the allocation process
      </v-snackbar>
  </div>
</template>


<script>
import * as mutationTypes from "../../../store/mutation-types";
export default {
  props: {
    show: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      snackbarTimeout: 4000,
      isInitiatingAllocation: false,
      allocationInitiated: false,
      allocationInitiationFailed: false,
      showInitiateAllocationDialog: false,
      valid: false,
      reasonForRejectingApplication: "",
      requiredRule: [v => !!v || "Required"]
    };
  },

  computed: {},

  methods: {
    initiateAllocation: function() {
      this.moveToAllocationPreview();
      this.closeInitiateAllocationDialog();
    },

    moveToAllocationPreview() {
      this.$store.commit(mutationTypes.UPDATE_ALLOCATION_FORM_STEPPER, 2);
    },

    closeInitiateAllocationDialog: function() {
      this.showInitiateAllocationDialog = false;
    }
  }
};
</script>


<style lang="sass" scoped>

</style>



