<template>
  <div class="p0"> 
    <v-card>
      <v-card-title class="d-flex justify-space-between bg-csp-light-blue p5 pt10">
          <h5 class="white--text font-size-18px">ALLOCATION PREVIEW</h5>
      </v-card-title>
        <!-- Content -->
      <v-card-text>
        <!-- IPO Application preview -->
        <v-container fluid class="p0">
          <div>
            <ul class="list-group list-group-flush black--text">
              <li class="list-group-item font-weight-bold pt5 pb5">IPO NAME
                <!-- Index Change -->
                <span class="ml-auto font-weight-normal">
                  <span>MTN Nigeria LTD</span>
                </span>
              </li>
                <li class="list-group-item font-weight-bold pt5 pb5">ALLOCATION DATE
                <!-- Index Change -->
                <span class="ml-auto font-weight-normal">
                  <span>24th May, 2018</span>
                </span>
              </li>
              <li class="list-group-item font-weight-bold pt5 pb5">SHARES OUTSTANDING
                <!-- Index Change -->
                <span class="ml-auto font-weight-normal">
                  <span >3,000,000</span>
                  <!-- <span v-else>0</span> -->
                </span>
              </li>
                <li class="list-group-item font-weight-bold pt5 pb5">PRICE PER SHARE
                <!-- Index Change -->
                <span class="ml-auto font-weight-normal">
                  <span >&#8358;29.90</span>
                  <!-- <span v-else>0</span> -->
                </span>
              </li>
              <li class="list-group-item font-weight-bold pt5 pb5">SHARES ALLOCATED
                <!-- Index Change -->
                <span class="ml-auto font-weight-normal">
                  <span>2,500,000</span>
                </span>
              </li>
              <li class="list-group-item font-weight-bold pt5 pb5">UNALLOCATED SHARES
                <!-- Index Change -->
                <span class="ml-auto font-weight-normal">
                  <span>500,000</span>
                </span>
              </li>
              <li class="list-group-item font-weight-bold pt5 pb5">NUMBER OF SUBSCRIBERS
                <!-- Index Change -->
                <span class="ml-auto font-weight-normal">
                  <span>530,124</span>
                </span>
              </li>
              <li class="list-group-item font-weight-bold pt5 pb5">NOTES
                <!-- Index Change -->
                <span class="ml-auto font-weight-normal">
                  <span>The IPO is undersubcribed by 500,000 units</span>
                </span>
              </li>
                
            </ul>
          </div>
        </v-container>
        <!-- EOF IPO Application preview -->

        <!-- Buttons -->
        <v-container  fluid class="p0 mt40">
            <v-btn small class="darken-1 white--text"
            style="background: #4c7396; color: #FFFFFF"
                  @click="acceptAllocation()">
                  ACCEPT ALLOCATION DATA 
            </v-btn>
              <v-btn small class="darken-1 white--text"
            style="background: #4c7396; color: #FFFFFF"
                  @click="viewAllocationPreviewDetails()">
                  VIEW ALLOCATION DETAILS
            </v-btn>
              <v-btn small class="error darken-1 white--text"
                  @click="goBack()">
                  CANCEL ALLOCATION
            </v-btn>
        </v-container>
        <!-- EOF Buttons -->
      </v-card-text>
    </v-card>

    <!-- Dialog for allocation preview details -->
    <allocation-preview-details></allocation-preview-details>
  </div>
</template>


<script>
import AllocationPreviewDetails from "./dialogs/AllocationPreviewDetails";
export default {
  data() {
    return {
      showAllocationPreviewDialog: false
    };
  },

  components: {
    AllocationPreviewDetails
  },

  methods: {
    viewAllocationPreviewDetails: function() {
      document.querySelector("#openAllocationPreviewDetailsDialog").click();

      /**
       * Stopping the event propagation because of the auto-close quirk that vuetify's dialog
       * popup has if the click event is not triggered from within the activator slot of the
       * dialog component
       */
      event.stopPropagation();
    },

    acceptAllocation: function() {},

    goBack() {
      this.$router.push({ name: "initiate-allocation" });
    }
  }
};
</script>


<style lang="sass" scoped>

</style>



