import getters from "./issuer.getters";
import mutations from "./issuer.mutations";

import * as mutationTypes from "../../mutation-types";

// Initial Store State
export const state = () => ({
  user: {},
  users: [],
  ipos: [],
  applications: [],
  serviceRequests: [],
  role: {},
  allocation: {
    allocationFormStepper: 0,
    ipo_id: "",
    cutOffPrice: "",
    allocation_details: {}
  }
});

const allMutations = {
  ...mutations,

  /**
   * Reset the issuing house module store to its initial state when logging out a user
   * @param state
   */
  [mutationTypes.CLEAR_ISSUING_HOUSE_MODULE_STORE](currentState) {
    const initialState = state();
    Object.keys(initialState).forEach(key => {
      currentState[key] = initialState[key];
    });
  }
};

export default {
  state: state(),
  getters,
  mutations: allMutations
};



