<template>
    <v-container fluid class="pt0">
    <!-- Add user button -->
    <v-card class="elevation-0 mb0 row d-flex flex-column ml1 mr1"
      style="background: transparent; border: none">
      <v-container fluid class="pl1 pr1 pt2">
        <v-layout row>
          <v-flex xs5 class="d-flex align-items-end">
             <!-- <h3 class="csp-light-blue-text font-size-20">Batches</h3> -->
          </v-flex>

          <v-flex xs7 d-flex class="">
            <v-btn :to="'/create-service-request'" info  class="fund-account-btn font-size-12 p0 ml-auto mb0 mr0">
              New Service Rewuest
            </v-btn>
          </v-flex>
        </v-layout>

        <v-layout row>
          <v-flex xs5 class="d-flex align-items-end">
             <h3 class="csp-light-blue-text font-size-20">Service Requests</h3>
          </v-flex>

          <v-flex xs7 d-flex class="">
            <v-container fluid class="p0">
              <v-layout row class="">
                <v-flex d-flex xs6 class="">
                </v-flex>
                <v-flex xs6>
                  <v-text-field
                    append-icon="search"
                    label="SEARCH"
                    hide-details
                    class="xs4 font-size-11"
                    v-model="search">
                  </v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
          </v-flex>
        </v-layout>
      </v-container>
    </v-card>
    
    <!-- Table -->
    <v-card class="elevation-0 mb10">
      <div class="heightt-300px " style="overflow-y: scroll"> <!-- Used so the market data table scrolls -->
         <v-data-table
        hide-actionss
        select-all="blue"
        v-model="selected"
        selected-key="id"
        :items="serviceRequests"
        :search="search"
        :loading="loadingDataTable"
        :rows-per-page-items="rowsPerPageItems"
        class="elevation-1 marketDataTable table-striped table-bordered">
        <template slot="headers" scope="props">
          <tr class="bg-csp-light-blue marketDataTableHeader">
            <th class="text-left">SUBJECT</th>
            <th class="text-left">STATUS</th>
            <th class="text-center width-80px white--text ">ACTIONS</th>
          </tr>
        </template>

        <template slot="items" scope="props">
          <tr :active="props.selected" @click="props.selected = !props.selected">
              <td>{{ props.item.subject }}</td>
              <td>{{ props.item.status_text }}</td>
              <td class="font-size-10 text-center width-80px d-flex align-center justify-center" style="padding: 0px; border: 0px">
                <v-btn
                  data-toggle="tooltip" data-placement="top" title="VIEW"
                  icon class="m0 p0 width-30px height-30px"
                  @click="showRequestDialog('VIEW',props.item, $event)">
                  <v-icon class="color-csp-blue ml4 fa fa-eye fa-2x btn-icon font-weight-bold font-size-18">
                  </v-icon>
                </v-btn>
              </td>
          </tr>
        </template>
      </v-data-table>
      </div>
    </v-card>

    <!-- Dialog for viewing a batch -->
    <!-- <view-batch
      :batch="batch"
      :show="showViewBatchDialog">
    </view-batch> -->
    <view-service-request
        :request="request"
        :show="showViewRequestDialog">
    </view-service-request>
  </v-container>
</template>


<script>
import ViewServiceRequest from "./ViewServiceRequest";
import { mapGetters } from "vuex";

export default {
  data() {
    return {
      search: "",
      request: {},
      showViewRequestDialog: false,
      selected: [],
      loadingDataTable: false,
      rowsPerPageItems: [10, 15, 20, 25, { text: "All", value: -1 }]
    };
  },

  components: {
    ViewServiceRequest
  },

  computed: {
    ...mapGetters({
      serviceRequests: "allServiceRequests"
    })
  },

  methods: {
    /**
     * Used to toggle the select all functionality
     */
    toggleAll() {
      if (this.selected.length) this.selected = [];
      else this.selected = this.applications.slice();
    },

    /**
     * Show the dialog box for requests
     */
    showRequestDialog(type, request, event) {
      // Set the selected user so the popup dialog can be populated with the selected user
      this.request = request;

      // Display the edit popup modal/dialog
      if (type === "VIEW") {
        document.querySelector("#openViewRequestDialog").click();
      }

      /**
       * Stopping the event propagation because of the auto-close quirk that vuetify's dialog
       * popup has if the click event is not triggered from within the activator slot of the
       * dialog component
       */
      event.stopPropagation();
    }
  }
};
</script>


<style scoped lang="sass">
  .fund-account-btn
    background: #324e85 !important
    border-radius: 5px
    border: none
    padding: 5px 10px !important
  .marketDataTable
    margin-bottom: 0px !important

  .marketDataTableHeader,
  .marketDataTableBody
    height: 25px

  .marketDataTableHeader
    th
      padding: 5px 5px !important
      color: #FFFFFF !important
      font-weight: bold
      font-size: 10px

  .marketDataTableBody
    border-bottom: none !important
    &:hover
      background: #CCC !important
      cursor: pointer

  .marketDataTableBody
    td
      padding: 5px 5px !important
      height: 25px
      color: #31708f
    td:last-child
      border-bottom: 0px solid transparent !important
  .btn__content
    padding: 0px !important
  .eemulate-link
    &:hover
      transition: all .2s
      text-decorations: underline
      border-bottom: 2px solid #1a2155
      padding-bottom: 2px
      cursor: pointer
      background: #EEEEEE

  .selectedRow
    background: #b0bed9 !important
    font-weight: bold
</style>


