/**
 * Getters are used in VUEX to "get" data from the store.
 * They allow for some sort of data transformation before returning the store data
 */

import { ChartService } from "../../../services/ChartService";
import {
  pieChartDummyData,
  barChartDummyData,
  ipoVolumeBarChartDummyData
} from "../../../data-mocks/graphs";
import { TOP_AGENTS, NEWS_FEED } from "../../../data-mocks/dashboard";

const getters = {
  userID: state => {
    return state.user.id;
  },

  user: state => {
    return state.user;
  },

  ipos: state => {
    return state.ipos;
  },

  /**
   * Get the data used to plot the ipo volume summary graph
   */
  ipoVolumeSummaryChartData: (state, getters) => {
    // Note the this data is currently a mock, and should be replaced with proper data from another getter
    const chartData = ChartService.getCspBarChart(ipoVolumeBarChartDummyData);
    return chartData;
  },

  /**
   * Get the top agents by volume for a particular IPO
   */
  topAgents: (state, getters) => {
    return TOP_AGENTS;
  },

  /**
   * Get the data used to plot the bar chart showing application statuses (Pending, approved and rejected)
   */
  applicationStatusBarChartData: (state, getters) => {
    // Note the this data is currently a mock, and should be replaced with proper data.
    const chartData = ChartService.getCspBarChart(barChartDummyData);
    return chartData;
  },

  /**
   * Get the data to be plotted as the pie chart on the dashboard of the agent
   */
  applicationTypeSummaryPieChartData: (state, getters) => {
    // Note the this data is currently a mock, and should be replaced with proper data.
    const chartData = ChartService.getCspPieChart(pieChartDummyData);
    return chartData;
  },

  /**
   * Get system news feeds to be displayed on the dashboard
   */
  newsFeed: (state, getters) => {
    return NEWS_FEED;
  },

  /**
   * Get all service requests pertaining to the particular issuing house
   */
  allServiceRequests: (state, getters) => {
    let requests = state.serviceRequests;

    // Only get requests that belong to the authenticated issuing house
    let filteredRequests = requests.filter(
      request => request.issuer_id === getters.userID
    );

    requests = requests.map(request => {
      if (request.processed) {
        request.status_text = "PROCCESSED";
      } else {
        request.status_text = "PENDING";
      }
      return request;
    });

    return requests;
  },

  // Get the current index of the allocation form
  allocationFormStepper: state => {
    return state.allocation.allocationFormStepper;
  },

  // get the entire allocation slice of state
  allocation: state => {
    return state.allocation;
  },

  allocationDetails: state => {
    return [];
  }
};

export default getters;



