<template>
    <v-container fluid grid-list-xl>
        <v-layout row wrap class="">
        
        <v-flex xs12 class="mb5">
            <dashboard-header></dashboard-header>
        </v-flex>

        <!-- Ipo Volume Summary -->
        <v-flex xs12 md8 lg8 class="mb5">
          <ipo-volume-summary
            :applications="applications"
            :ipoVolumeSummaryChartData="ipoVolumeSummaryChartData">
          </ipo-volume-summary>
        </v-flex>

         <!-- Application Type Summary -->
        <v-flex xs12 md4 lg4 class="mb5">
          <top-agents :agents="topAgents"></top-agents>
        </v-flex>

        <!-- Application Status Summary -->
        <v-flex xs12 md4 lg4 class="mb5">
          <application-status-summary
            :applications="applications"
            :applicationStatusChartData="applicationStatusChartData">
          </application-status-summary>
        </v-flex>

        <!-- Batch Payment Summary -->
        <v-flex xs12 md4 lg4 class="mb5">
           <application-type-summary
            :applications="applications"
            :applicationTypeSummaryChartData="applicationTypeSummaryChartData">
           </application-type-summary>
        </v-flex>

        <!-- System Information -->
        <v-flex xs12 md4 lg4 class="mb5">
            <system-information
              :newsFeed="newsFeed">
            </system-information>
        </v-flex>

        <!-- Business News -->
        <v-flex xs12 md4 lg4 class="mb5">
            
        </v-flex>

        </v-layout>
  </v-container>
</template>

<script>
import { mapGetters } from "vuex";

import DashboardHeader from "./DashboardHeader";
import ApplicationTypeSummary from "./ApplicationTypeSummary";
import ApplicationStatusSummary from "./ApplicationStatusSummary";
import IpoVolumeSummary from "./IpoVolumeSummary";
import TopAgents from "./TopAgents";
import SystemInformation from "./SystemInformation";

// import { newsFeed } from "../../../../data-mocks/eipo/dashboard";

export default {
  components: {
    DashboardHeader,
    ApplicationTypeSummary,
    ApplicationStatusSummary,
    IpoVolumeSummary,
    TopAgents,
    SystemInformation
  },

  data() {
    return {
      applications: []
    };
  },

  computed: {
    ...mapGetters({
      applicationTypeSummaryChartData: "applicationTypeSummaryPieChartData",
      applicationStatusChartData: "applicationStatusBarChartData",
      ipoVolumeSummaryChartData: "ipoVolumeSummaryChartData",
      topAgents: "topAgents",
      newsFeed: "newsFeed"
    })
  }
};
</script>

<style>

</style>



