/**
 * This service defines all the URL endpoints used in the application, so there is a single source * of truth if they need to be changed.
 *
 * @author Omadoye Abraham <omadoyeabraham@gmail.com>
 */

export const BaseApiUrl = "https://eipo-api.cardinalstone.com/api/";

export const AuthenticationUrl = BaseApiUrl + "issuingHouse/authenticate";
export const ChangePasswordUrl = BaseApiUrl + "changePassword";

// Service Requests
export const ServiceRequestResourceURL = BaseApiUrl + "serviceRequests";

// Allocation
export const InitiateAllocationURL = BaseApiUrl + "ipo/allocate";



