<template>
  <v-container fluid>
      <!-- MultiStep form used in the IPO allocation process -->
      <v-stepper v-model="allocationFormStepper">

        <!-- Allocation form headers -->
        <v-stepper-header>
            <v-stepper-step step="1" :complete="allocationFormStepper > 1">Initiate Allocation Process</v-stepper-step>
            <v-divider></v-divider>
            <v-stepper-step step="2" :complete="allocationFormStepper > 2">Set Cut-Off Price</v-stepper-step>
            <v-divider></v-divider>
            <v-stepper-step step="3" :complete="allocationFormStepper > 3">Preview Allocation Results</v-stepper-step>
        </v-stepper-header>
        <!-- EOF Allocation form headers -->

        <!-- Initiate Allocation Process -->
        <v-stepper-content step="1">
            <initiate-allocation-process></initiate-allocation-process>
        </v-stepper-content>
        <!-- EOF Initiate Allocation Process -->

        <!-- CutOff price -->
        <v-stepper-content step="2">
            <cut-off-price></cut-off-price>
        </v-stepper-content>
        <!-- EOF CutOff Price -->

        <!-- Preview allocation results -->
        <v-stepper-content class="p0" step="3">
          <preview-allocation-results></preview-allocation-results>
        </v-stepper-content>
        <!-- EOF preview allocation results -->

      </v-stepper>
      <!-- EOF MultiStep form used in allocating IPO shares -->

  </v-container>
</template>


<script>
import { mapGetters } from "vuex";
import * as mutationTypes from "../../store/mutation-types";
import InitiateAllocationProcess from "./InitiateAllocation";
import CutOffPrice from "./CutOffPrice";
import PreviewAllocationResults from "./PreviewAllocationResults";

export default {
  components: {
    InitiateAllocationProcess,
    CutOffPrice,
    PreviewAllocationResults
  },

  computed: {
    ...mapGetters({
      _allocationFormStepper: "allocationFormStepper"
    }),

    allocationFormStepper: {
      get: function() {
        return this._allocationFormStepper;
      },

      set: function(newValue) {
        this.$store.commit(
          mutationTypes.UPDATE_ALLOCATION_FORM_STEPPER,
          newValue
        );
      }
    }
  }
};
</script>


<style lang="sass" scoped>

</style>



