<template>
    <v-container fluid class="height-400px" justify-center align-items-center>
        <v-layout row text-center justify-center>
            <v-flex xs12 sm10 lg6>
                
                <v-layout row class="pl10 mb40">
                    <div class="font-size-16">INITIATE ALLOCATION PROCESS</div>
                    <div>
                        <strong>
                            You are about to initiate the IPO allocation process.
                        </strong>
                    </div>
                </v-layout>

                <!-- Form -->
                 <v-layout row justify-center class="d-flex">
                    <v-flex>
                        <v-form v-model="valid" ref="initiateAllocationForm" class="w100p pl5">
                            <v-select class="" :label="'SELECT IPO'" :items="ipos" 
                                v-model="selectedIpo" v-validate="'required'" name="ipo" :rules="requiredRule">
                            </v-select>
                        </v-form> 
                    </v-flex>
                 </v-layout>
                 
                 <!-- Buttons -->
                 <v-layout  row class="mt20">
                    <v-btn small class="red darken-1 white--text"
                        @click="cancelAddApplicationsDialog()">
                        CANCEL
                    </v-btn>
                    <v-btn small info class=" darken-1 white--text"
                        @click="initiateAllocation($event)"
                        :disabled="initiatingAllocation" :loading="initiatingAllocation">
                        INITIATE ALLOCATION
                    </v-btn>
                </v-layout>
            </v-flex>
        </v-layout>

        <initiate-application-dialog>
        </initiate-application-dialog>
        
    </v-container>
</template> 


<script>
import { mapGetters } from "vuex";
import * as mutationTypes from "../../store/mutation-types";
import InitiateApplicationDialog from "./dialogs/initiateAllocationDialog";

export default {
  components: {
    InitiateApplicationDialog
  },
  data() {
    return {
      snackbarTimeout: 4000,
      initiatingAllocation: false,
      errorMessage: "error occured while initiating the allocation process",
      isInitiatingAllocation: false,
      valid: false,
      selectedIpo: null,
      requiredRule: [v => !!v || "Required"]
    };
  },

  computed: {
    ...mapGetters({
      ipos: "ipos",
      userID: "userID"
    })
  },

  methods: {
    initiateAllocation($event) {
      this.$validator.validateAll().then(result => {
        if (!result) {
          // Display the validation error for each form component
          let formComponents = this.$refs.initiateAllocationForm.$children;
          formComponents.forEach(formComponent => {
            formComponent.shouldValidate = true;
          });
        } else {
          this.$store.commit(
            mutationTypes.UPDATE_ALLOCATION_SELECTED_IPO,
            this.selectedIpo
          );
          this.openInitiateAllocationDialog($event);
        }
      });
    },

    openInitiateAllocationDialog(event) {
      document.querySelector("#openInitiateAllocationDialog").click();

      /**
       * Stopping the event propagation because of the auto-close quirk that vuetify's dialog
       * popup has if the click event is not triggered from within the activator slot of the
       * dialog component
       */
      event.stopPropagation();
    }
  }
};
</script>

<style lang="sass" scoped>

</style>



