<template>
     <v-container fluid class="height-400px" justify-center align-items-center>
        <v-layout row text-center justify-center>
            <v-flex xs12 sm10 lg6>
                
                <v-layout row class="pl10 mb40">
                    <div class="font-size-16"> ALLOCATION PROCESS</div>
                    <div>
                        <strong>
                            Provide the cut off price at which shares will be allocated.
                        </strong>
                    </div>
                </v-layout>

                <!-- Form -->
                 <v-layout row justify-center class="d-flex">
                    <v-flex>
                        <v-form v-model="valid" ref="cutOffPriceForm" class="w100p pl5">
                            
                            <v-text-field   :label="'CUT-OFF PRICE'" v-model="cutOffPrice" 
                            :type="'number'" :rules="requiredRule"  v-validate="'required'" name="cutOffPrice" 
                            :disabled="inPreviewState">
                            </v-text-field>
              
                        </v-form> 
                    </v-flex>
                 </v-layout>
                 
                 <!-- Buttons -->
                 <v-layout  row class="mt20">
                    <v-btn small class="red darken-1 white--text"
                        @click="cancelAddApplicationsDialog()">
                        CANCEL
                    </v-btn>
                    <v-btn small info class=" darken-1 white--text"
                        @click="initiateAllocation($event)"
                        :disabled="initiatingAllocation" :loading="initiatingAllocation">
                        INITIATE ALLOCATION
                    </v-btn>
                </v-layout>
            </v-flex>
        </v-layout>
        
    </v-container>
</template>


<script>
import * as mutationTypes from "../../store/mutation-types";
import { AllocateIpo } from "../../services/AllocationService";
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      cutOffPrice: "",
      valid: false,
      snackbarTimeout: 4000,
      isInitiatingAllocation: false,
      allocationInitiated: false,
      allocationInitiationFailed: false,
      showInitiateAllocationDialog: false,
      initiatingAllocation: false,
      inPreviewState: false,
      requiredRule: [v => !!v || "Required"]
    };
  },
  computed: {
    ...mapGetters({
      allocation: "allocation",
      userID: "userID"
    })
  },
  methods: {
    initiateAllocation() {
      this.$validator.validateAll().then(result => {
        if (!result) {
          // Display the validation error for each form component
          let formComponents = this.$refs.cutOffPriceForm.$children;
          formComponents.forEach(formComponent => {
            formComponent.shouldValidate = true;
          });
        } else {
          this.$store.commit(
            mutationTypes.UPDATE_ALLOCATION_CUT_OFF_PRICE,
            this.cutOffPrice
          );
          this.isInitiatingAllocation = true;
          const allocationRequest = {
            user_id: this.userID,
            ipo_id: this.allocation.ipo_id,
            share_price: this.allocation.cutOffPrice
          };

          AllocateIpo(allocationRequest)
            .then(response => {
              this.isInitiatingAllocation = false;
              console.log(response);
            })
            .catch(err => {
              this.isInitiatingAllocation = false;
              console.log(err);
            });

          this.moveToAllocationResultsPreview();
        }
      });
    },

    moveToAllocationResultsPreview() {
      this.$store.commit(mutationTypes.UPDATE_ALLOCATION_FORM_STEPPER, 3);
    }
  }
};
</script>

<style lang="sass" scoped>

</style>



