import {
  IssuingHouseAuthenticateResponse,
  Ipo
} from "../models/issuing-house.interface";

/**
 * Transformer for authentication data.
 *
 * This transforms the data from the API into a consistent data structure that the frontend can rely on
 */

export const TransformLoginResponse = (
  apiResponse
): IssuingHouseAuthenticateResponse => {
  const applications = apiResponse.applications ? apiResponse.applications : [];
  const serviceRequests = apiResponse.serviceRequests
    ? apiResponse.serviceRequests
    : [];
  const user = apiResponse.user ? apiResponse.user : {};
  const users = apiResponse.users ? apiResponse.users : [];
  // const ipos = apiResponse.ipos ? apiResponse.ipos : [];
  const role = apiResponse.role ? apiResponse.role : {};

  // Dummy data
  const ipos = [
    {
      id: 1,
      text: "MTN NIGERIA",
      value: 1
    },
    {
      id: 2,
      text: "DROPBOX",
      value: 2
    }
  ] as Array<Ipo>;

  return {
    applications,
    serviceRequests,
    user,
    users,
    ipos,
    role
  };
};



