import axios from "axios";
import * as ApiUrls from "./ApiUrlService";

/**
 * Create a new service request
 * @param data
 */
export const CreateServiceRequest = data => {
  return axios({
    method: "post",
    url: ApiUrls.ServiceRequestResourceURL,
    data
  });
};

/**
 * Get all service requests pertaining to an issuing house
 * @param data
 */
export const GetServiceRequests = issuer_id => {
  return axios({
    method: "get",
    url: ApiUrls.ServiceRequestResourceURL
    // + issuer_id
  });
};



