<template>
    <v-navigation-drawer
      class="pb-0 pl5"
      persistent
      height="100%"
      clipped
      enable-resize-watcher
      :mini-variant.sync="mini"
      v-model="showSidebar">

      <!-- Section Header -->
      <div
        class="sidebar-header d-flex justify-content-between"
        v-if="mini===false">
        <span>Issuing House Module</span>
        <router-link to="/dashboard">
          <i class="fa fa-home sidebar-icon" aria-hidden="true"></i>
        </router-link>
      </div>
      <SidebarList :SidebarItems="sidebarItems"></SidebarList>

    </v-navigation-drawer>
</template>

<script>
import SidebarList from "./SidebarList";

import { sidebarItems } from "../../services/SidebarService.ts";
sidebarItems;

export default {
  components: {
    SidebarList
  },

  props: ["isVisible"],

  created() {},

  beforeDestroy() {},

  // Component specific data
  data() {
    return {
      sidebarItems,
      showSidebar: this.isVisible,
      right: null,
      mini: false
    };
  },

  watch: {
    isVisible: function() {
      this.showSidebar = this.isVisible;
    }
  },

  // Computed properties gotten mainly from the global application store
  computed: {},

  methods: {}
};
</script>

<style scoped lang="sass">
  .toolbar__content
    border-bottom: 1px solid #ccc
  .sidebar-header
    font-size: 2rem
    color: #4c7396 !important
    margin: 5px
    margin-top: 10px
    margin-bottom: 0
    border-bottom: 1px solid rgb(204,204,204)

  .sidebar-icon
      color: #8fb0cc !important


</style>



