<!--
  This component displays the Login Page, and handles user authentication

  @author Omadoye Abraham <omadoyeabraham@gmail.com>
 -->

<template>
  <div class="h-100 mb0" id="loginWrapper" style='margin-bottom: 0px !important'>
    <!-- Logo at the top of the page -->
    <img id="company-img" class="img-responsive ml30" src="../assets/img/cardinalstone-white.png" />
    <!--Login Card -->
    <div class="container-fluid d-flex flex-column justify-content-center align-items-center" id="inner-login-wrapper">

      <v-layout row>
        <div class="card card-container elevation-24" id="loginCard">
          <v-layout row class="d-flex column">
            <v-flex class="pr0 text-center">
                <img id="company-img" class="img-responsive"
                src="../assets/img/logo.png" width='50px' />
            </v-flex>
            <v-flex class="align-self-center pl0">
                <span class="text-csp-blue font-size-20">Log-in to your account</span>
            </v-flex>
          </v-layout>
            
            <v-form v-model="valid" ref="loginForm" class="form-signin">

                 <v-alert error
                  icon="new_releases" :value="true"
                  v-if="authError">
                  Invalid Email or Password
                 </v-alert>

                <v-text-field
                  label="EMAIL"
                  :append-icon="'account_circle'"
                  v-model="email"
                  v-validate="'required'"
                  :rules="emailRules"
                  autocomplete="nope">
                </v-text-field>
                <v-text-field
                  label="PASSWORD"
                  v-model="password"
                  v-validate="'required'"
                  :append-icon="hidePassword ? 'visibility' : 'visibility_off'"
                  :append-icon-cb="() => (hidePassword = !hidePassword)"
                  :type="hidePassword ? 'password' : 'text'"
                  :rules="passwordRules"
                  autocomplete="nope">
                </v-text-field>

                <!-- Loading Icon when signing in-->
                <v-layout v-if="isAuthenticating"  row class=" d-flex justify-center ">
                  <v-flex class="xs6 d-flex justify-center align-center">
                    <v-progress-circular indeterminate class="primary--text height-20px width-20px"></v-progress-circular>
                    <span class="font-size-12 ml5">Signing in</span>
                  </v-flex>
                </v-layout>

                <v-btn info
                  class="blue darken-4 font-size-13 mt10 mb20 login-btn"
                  :loading="isAuthenticating"
                  :disabled="isAuthenticating"
                  @click="login()">
                LOGIN
                </v-btn>

            </v-form>

        </div><!-- /card-container -->
      </v-layout>

    </div>

    <change-default-password
      :show="showChangeDefaultPassword">
    </change-default-password>

  </div>

</template>

<script>
import { required } from "vuelidate/lib/validators";
import { AuthenticateUser } from "../services/UserService";
import { SaveServiceRequestsToStore } from "../services/MutationServices";
import ChangeDefaultPassword from "./ChangeDefaultPassword";
import ConfigDataService from "../services/ConfigDataService";
import { TransformLoginResponse } from "../transformers/authentication.transformers";
import * as mutationTypes from "../store/mutation-types";

export default {
  components: {
    ChangeDefaultPassword
  },
  data() {
    return {
      valid: false,
      name: "EIPO ISSUING HOUSE MODULE",
      email: "",
      password: "",
      hidePassword: true,
      isAuthenticating: false,
      emailRules: [email => !!email || "Required"],
      passwordRules: [password => !!password || "Required"],
      authError: false,
      showChangeDefaultPassword: false
    };
  },

  methods: {
    login: function() {
      // Set the Authorization token to be used for future API requests (just dummy data currently)
      window.sessionStorage.accessToken =
        Math.random()
          .toString(36)
          .substring(2, 15) +
        Math.random()
          .toString(36)
          .substring(2, 15);

      // Remove this line and uncomment chunk below for actual authentication
      // this.$router.push({ name: "Dashboard" });

      this.$validator.validateAll().then(result => {
        if (!result) {
          // Display the validation error for each form component
          let formComponents = this.$refs.loginForm.$children;
          formComponents.forEach(formComponent => {
            formComponent.shouldValidate = true;
          });
        } else {
          this.isAuthenticating = true;

          const credentials = {
            email: this.email,
            password: this.password,
            role_id: ConfigDataService.issuerRoleID
          };

          AuthenticateUser(credentials)
            .then(response => {
              // Ensure that the response is transformed to a consistent format expected by the frontend application
              const issuerData = TransformLoginResponse(response.data.data);
              this.isAuthenticating = false;
              this.authError = false;
              const isFirstLogin = issuerData.user.firstLogin === 0;

              // this.$store.commit(mutationTypes.SAVE_USER_TO_STORE, issuerData);

              // Get and save the service requests to the store
              SaveServiceRequestsToStore(issuerData.user.id);

              if (!isFirstLogin) {
                this.$store.commit(
                  mutationTypes.SAVE_USER_TO_STORE,
                  issuerData
                );
                this.$router.push({ name: "Dashboard" });
              } else {
                // Users logging in for the fisrt time must change their password
                this.showChangeDefaultPasswordDialog();
              }
            })
            .catch(err => {
              this.isAuthenticating = false;
              this.authError = true;
              console.log(err.response);
            });
        }
      });
    },

    showChangeDefaultPasswordDialog: function() {
      document.querySelector("#openChangeDefaultPasswordDialog").click();
    }
  },

  validations: {
    username: {
      required
    },
    password: {
      required
    }
  }
};
</script>

<!-- Scoped styles apply only to this component -->
<style scoped lang='scss'>
.is-danger {
  background: red;
}
#loginWrapper {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  min-height: 100%;

  background: -moz-linear-gradient(
    4deg,
    #3679ff 0%,
    #1f3378 53%,
    #1a2155 67%
  ); /* ff3.6+ */
  background: -webkit-gradient(
    linear,
    left top,
    right top,
    color-stop(0%, #3679ff),
    color-stop(53%, #1f3378),
    color-stop(67%, #1a2155)
  ); /* safari4+,chrome */
  background: -webkit-linear-gradient(
    4deg,
    #3679ff 0%,
    #1f3378 53%,
    #1a2155 67%
  );
  /* safari5.1+,chrome10+ */
  background: -o-linear-gradient(
    4deg,
    #3679ff 0%,
    #1f3378 53%,
    #1a2155 67%
  ); /* opera 11.10+ */
  background: -ms-linear-gradient(
    4deg,
    #3679ff 0%,
    #1f3378 53%,
    #1a2155 67%
  ); /* ie10+ */
  background: linear-gradient(86deg, #3679ff 0%, #1f3378 53%, #1a2155 67%);
  /* w3c */
  filter: progid:DXImageTransform.Microsoft.gradient(
      startColorstr="#3679FF",
      endColorstr="#1A2155",
      GradientType=1
    );
  /* ie6-9 */
  *background-image: url("../../static/img/background2.png");
  background-size: cover;
  background-position: center center;
}
.btn-link:hover {
  cursor: pointer;
  text-decoration: none;
  /* color: rgb(104, 145, 162) !important; */
}
.btn-link:hover v-icon {
  /* color: rgb(104, 145, 162) !important */
}
.card-container.card {
  border-radius: 10px;
  min-width: 450px;
  max-width: 500px;
  padding: 30px 40px;
}
input {
  min-width: 250px;
}
.btn {
  font-weight: 700;
  height: 36px;
  margin: 0px auto;
  -moz-user-select: none;
  -webkit-user-select: none;
  user-select: none;
  cursor: default;
}
.card {
  background-color: #f7f7f7; /* just in case there no content */
  padding: 20px 25px 30px;
  *margin: 0 auto 25px;
  margin-top: 20%;
  /* shadows and rounded borders */
  -moz-border-radius: 2px;
  -webkit-border-radius: 2px;
  border-radius: 2px;
  -moz-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
}
.login-btn {
  border-radius: 7px;
  width: 50%;
}
#company-img {
  margin: 0 auto;
}
/* * Form styles */
.profile-name-card {
  font-size: 16px;
  font-weight: bold;
  text-align: center;
  margin: 10px 0 0;
  min-height: 1em;
}
.reauth-email {
  display: block;
  color: #404040;
  line-height: 2;
  margin-bottom: 10px;
  font-size: 14px;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.form-signin #inputEmail,
.form-signin #inputPassword {
  direction: ltr;
  height: 44px;
  font-size: 16px;
}
.form-signin input[type="email"],
.form-signin input[type="password"],
.form-signin input[type="text"],
.form-signin button {
  *width: 100%;
  display: block;
  margin-bottom: 10px;
  z-index: 1;
  position: relative;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.form-signin .form-control:focus {
  border-color: rgb(104, 145, 162);
  outline: 0;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075),
    0 0 8px rgb(104, 145, 162);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgb(104, 145, 162);
}
.btn.btn-signin {
  background-color: rgb(26, 33, 85); /* CardinalStone blue */
  padding: 0px;
  font-weight: 700;
  font-size: 14px;
  height: 36px;
  -moz-border-radius: 3px;
  -webkit-border-radius: 3px;
  border-radius: 10px;
  border: none;
  -o-transition: all 0.218s;
  -moz-transition: all 0.218s;
  -webkit-transition: all 0.218s;
  transition: all 0.218s;
}
.btn.btn-signin:hover,
.btn.btn-signin:active,
.btn.btn-signin:focus {
  background-color: rgb(
    71,
    77,
    118
  ); /* A lighter shade of CardinalStone blue */
}
.forgot-password {
  color: rgb(26, 33, 85); /* CardinalStone blue */
}
.forgot-password:hover,
.forgot-password:active,
.forgot-password:focus {
  color: rgb(71, 77, 118);
  /* A lighter shade of CardinalStone blue */
  text-decoration: none;
}
.contact-company {
  margin-top: 30px;
}
.contact-company a {
  color: rgb(26, 33, 85); /* CardinalStone blue */
}
.contact-company a:hover {
  color: rgb(71, 77, 118);
  /* A lighter shade of CardinalStone blue */
  text-decoration: none;
}
#inner-login-wrapper {
  *background-image: url("../../static/img/background2.png");
  *z-index: -1;
}
#footer-links-wrapper {
  position: absolute;
  bottom: 10px;
  left: 50px;
  right: 50px;
  *background: red;
  color: #ffffff;
}
.social-icons-wrapper {
  font-size: 25px;
}
.social-icons-wrapper i {
  color: #fff !important;
}
.social-icons-wrapper {
  i {
    &:hover {
      color: #aaa !important;
      cursor: pointer;
    }
  }
}
</style>



