<!--
    Component which houses the pie chart showing the application type summary
-->
<template>
  <v-card class="white lighten-2 card height-250px" dark>

    <div class="w100p bg-csp-light-blue p10">
      <div class="text-uppercase">TOP AGENTS BY VOLUME</div>
    </div>

    <!-- Card body -->
    <div class="card-block" >
      <ul class="list-group list-group-flush black--text">
        <li class="list-group-item font-weight-bold pt5 pb5" v-for="(agent, index) in agents" :key="index">{{agent.name}}
          <span class="ml-auto font-weight-normal">
            <span class="">{{agent.volume | currency('', 0)}}</span>
          </span>
        </li>
      </ul>
    </div>

  </v-card>
</template>


<script>
export default {
  props: ["agents"]
};
</script>


<style lang="sass" scoped>
  .card-block
    overflow-y: scroll
</style>



