import * as mutationTypes from "../../mutation-types";
import { IssuingHouseAuthenticateResponse } from "../../../models/issuing-house.interface";

const mutations = {
  // Save the users data to the store
  [mutationTypes.SAVE_USER_TO_STORE](
    state,
    userData: IssuingHouseAuthenticateResponse
  ) {
    Object.keys(userData).forEach(key => {
      state[key] = userData[key];
    });
  },

  // Save service requests to store
  [mutationTypes.SAVE_SERVICE_REQUESTS_TO_STORE](state, serviceRequests) {
    state.serviceRequests = serviceRequests;
  },

  // Update the form stepper index for allocation
  [mutationTypes.UPDATE_ALLOCATION_FORM_STEPPER](
    state,
    newStepperIndex: number = 0
  ) {
    state.allocation.allocationFormStepper = newStepperIndex;
  },

  // Update the ipo id index for allocation
  [mutationTypes.UPDATE_ALLOCATION_SELECTED_IPO](state, ipoId: number) {
    state.allocation.ipo_id = ipoId;
  },

  // Update the cutOffPrice index for allocation
  [mutationTypes.UPDATE_ALLOCATION_CUT_OFF_PRICE](state, cutOffPrice: number) {
    state.allocation.cutOffPrice = cutOffPrice;
  }
};

export default mutations;



