<template>
  <div>
        <v-dialog v-model="openChangeDefaultPasswordDialog" lazy absolute persistent width="40%">
            <!--Button used to trigger the opening of the edit user dialog -->
            <v-btn id="openChangeDefaultPasswordDialog"
            style="display: none" color="primary" dark slot="activator" ref="triggerButton">
            </v-btn>

            <v-card>
            <v-card-title class="d-flex justify-center bg-csp-light-blue p5 pt10">
                <h5 class="white--text font-size-20px">SET A PASSWORD</h5>
            </v-card-title>
            <!-- Content -->
            <v-card-text>
                <v-layout>
                    <v-flex>
                        <strong>You are logging in for the first time. Please set your passowrd</strong>

                        <v-form v-model="valid" ref="changePasswordForm" class="w100p pl5">
                           <v-text-field
                            label="New Password"
                            v-model="newPassword"
                            :append-icon="hideNewPassword ? 'visibility' : 'visibility_off'"
                            :append-icon-cb="() => (hideNewPassword = !hideNewPassword)"
                            :type="hideNewPassword ? 'password' : 'text'"
                            :rules="passwordRules"
                            autocomplete="nope">
                            </v-text-field>
                            <v-text-field
                            label="Confirm Password"
                            v-model="confirmPassword"
                            :append-icon="hideConfirmPassword ? 'visibility' : 'visibility_off'"
                            :append-icon-cb="() => (hideConfirmPassword = !hideConfirmPassword)"
                            :type="hideConfirmPassword ? 'password' : 'text'"
                            :rules="passwordRules"
                            autocomplete="nope">
                            </v-text-field>
                        </v-form> 
                    </v-flex>
                </v-layout>

                <!-- Loading Icon when approving application-->
                <v-layout v-if="isChangingPassword"  row class=" d-flex justify-center ">
                    <v-flex class="xs6 d-flex justify-center align-center">
                        <v-progress-circular indeterminate class="primary--text height-20px width-20px"></v-progress-circular>
                        <span class="font-size-12 ml5">Changing Password...</span>
                    </v-flex>
                </v-layout>

                <!-- Buttons -->
                <v-container fluid class="p0 ">
                    <v-layout  row class="mt20">
                        <v-flex xs12 lg6>
                            <v-btn small block info class=" darken-1 white--text"
                            @click="changePassword()"
                            :disabled="isChangingPassword" :loading="isChangingPassword">
                            CHANGE PASSWORD
                            </v-btn>
                        </v-flex>
                        <v-flex xs12 lg6>
                            <v-btn small block class="red darken-1 white--text"
                            @click="cancelChangeDefaultPasswordDialog()">
                            CANCEL
                            </v-btn>
                        </v-flex>
                    </v-layout>
                </v-container>
            </v-card-text>
            </v-card>
        </v-dialog>

      <!-- Snackbar to show success message when default password is changed -->
      <v-snackbar success
        :timeout="snackbarTimeout"
        :top="true"
        :color="'success'"
        v-model="passwordChanged"
        >
        Your default password has been changed.
      </v-snackbar>

      <!-- Snackbar to show error message when changing password fails -->
      <v-snackbar error
        :timeout="snackbarTimeout"
        :top="true"
        :color="'error'"
        v-model="passwordChangeFailed"
        >
        {{ errorMessage }}
      </v-snackbar>
    </div>
</template>


<script>
import { ChangePassword } from "../services/UserService";
import { mapGetters } from "vuex";

export default {
  data() {
    return {
      valid: false,
      openChangeDefaultPasswordDialog: false,
      isChangingPassword: false,
      changePasswordError: false,
      errorMessage: "Default password could not be changed",
      passwordChanged: false,
      passwordChangeFailed: false,
      snackbarTimeout: 4000,
      newPassword: "",
      confirmPassword: "",
      passwordRules: [password => !!password || "Required"],
      hideConfirmPassword: true,
      hideNewPassword: true
    };
  },

  computed: {
    ...mapGetters({
      userID: "userID",
      user: "user"
    })
  },

  methods: {
    cancelChangeDefaultPasswordDialog() {
      this.openChangeDefaultPasswordDialog = false;
    },

    changePassword() {
      this.$validator.validateAll().then(result => {
        if (!result) {
          // Display the validation error for each form component
          let formComponents = this.$refs.changePasswordForm.$children;
          formComponents.forEach(formComponent => {
            formComponent.shouldValidate = true;
          });
        } else {
          this.isChangingPassword = true;

          const credentials = {
            // password: this.password,
            // confirmPassword: this.confirmPassword,
            // userID: userID,
            ...this.user,
            password: this.password
          };

          ChangePassword(credentials)
            .then(response => {
              this.isChangingPassword = false;
              this.passwordChanged = true;

              setTimeout(() => {
                // Move straight to the dashboard for now, since no authentication is in place
                this.$router.push({ name: "Dashboard" });
              }, 1000);
            })
            .catch(err => {
              this.isChangingPassword = false;
              this.passwordChangeFailed = true;
              this.errorMessage = "Could not change password";
              console.log(err.response);
            });
        }
      });
    }
  }
};
</script>


<style lang="sass" scoped>

</style>



