<template>
    <v-container class="pt0 mt10"
    fluid>
        <v-layout row>
            <v-flex sm12 lg6 class="mb10">
                <v-card>
                    <v-card-title primary-title class="font-weight-bold p2 d-flex justify-start">
                        <h5 class=" pt10 font-weight-bold pl5 mb-0 font-size-11 text-uppercase">
                            <span class="font-size-17 font-weight-normal">CREATE A SERVICE REQUEST</span>
                        </h5>
                    </v-card-title>
                    <v-card-media>
                        <v-container fluid class="pt0 pl5 pr5">
                            <v-layout row>
                                <v-form v-model="valid" ref="createServiceRequestForm" class="w100p">
                                <!-- Request Name -->
                                <v-flex xs6 class="height-55px mb6 pl10">
                                    <v-text-field  :label="'REQUEST SUBJECT'" v-model="requestSubject" :type="'text'" :rules="requiredRule"  
                                    v-validate="'required'" name="requestSubject" :disabled="inPreviewState">
                                    </v-text-field>
                                </v-flex>
                                
                                <!-- Request Description -->
                                <v-flex class="pl10">
                                    <v-text-field
                                    name="requestDescription"
                                    v-model="requestDescription"
                                    label="REQUEST DESCRIPTION"
                                    v-validate="'required'"
                                    :rules="requiredRule"
                                    multi-line
                                    ></v-text-field>
                                </v-flex>

                                </v-form>
                            </v-layout>

                            <!-- Loading Icon when creating batches-->
                            <v-layout v-if="isCreatingRequest"  row class=" d-flex justify-center ">
                                <v-flex class="xs6 d-flex justify-center align-center">
                                <v-progress-circular indeterminate class="primary--text height-20px width-20px"></v-progress-circular>
                                <span class="font-size-12 ml5">Creating New Service Request</span>
                                </v-flex>
                            </v-layout>

                            <!-- Buttons -->
                            <v-layout row class="mt20">
                                <v-btn style="background: #4c7396; color: #FFFFFF"
                                @click="createServiceRequest()" :loading="isCreatingRequest" :disabled="isCreatingRequest">
                                CREATE REQUEST
                                </v-btn>
                                <v-btn class="red darken-1 white--text" >
                                Cancel
                                </v-btn>
                            </v-layout>
                        </v-container>
                    </v-card-media>
                </v-card>
            </v-flex>
        </v-layout>

        <!-- Snackbar to show success message when batch is created -->
        <v-snackbar success
            :timeout="snackbarTimeout"
            :top="true"
            :color="'success'"
            v-model="requestCreated"
            >
           Service request has been created successfully
        </v-snackbar>

        <!-- Snackbar to show error message when batch creation fails -->
        <v-snackbar error
            :timeout="snackbarTimeout"
            :top="true"
            :color="'error'"
            v-model="requestCreationFailed"
            >
            {{ errorMessage }}
        </v-snackbar>
    </v-container>
</template>


<script>
import { mapGetters } from "vuex";
import { CreateServiceRequest } from "../../services/ServiceRequestService";
import ConfigData from "../../services/ConfigDataService";
import { SaveServiceRequestsToStore } from "../../services/MutationServices";

export default {
  data() {
    return {
      errorMessage: "Unable to create service request",
      snackbarTimeout: 4000,
      valid: false,
      inPreviewState: false,
      requestSubject: null,
      requestDescription: "",
      isCreatingRequest: false,
      requestCreated: false,
      requestCreationFailed: false,
      requiredRule: [v => !!v || "Required"]
    };
  },

  computed: {
    ...mapGetters({
      issuerID: "userID"
    })
  },

  methods: {
    createServiceRequest() {
      this.$validator.validateAll().then(result => {
        if (!result) {
          // Display the validation error for each form component
          let formComponents = this.$refs.createServiceRequestForm.$children;
          formComponents.forEach(formComponent => {
            formComponent.shouldValidate = true;
          });
        } else {
          const serviceRequest = {
            issuer_id: this.issuerID,
            registrar_id: ConfigData.defaultRegistrarID,
            subject: this.requestSubject,
            description: this.requestDescription,
            content: "default content",
            ipo_id: ConfigData.defaultIpoID,

            // These 3 need to be removed once Abiodun makes them nullable
            status: 1,
            processed: false,
            pending: true
          };

          console.log(serviceRequest);

          this.isCreatingRequest = true;
          CreateServiceRequest(serviceRequest).then(
            response => {
              this.isCreatingRequest = false;
              this.requestCreated = true;

              // Refresh the List of requests
              this.refreshServiceRequests();
              this.$router.push({ name: "ListServiceRequests" });
            },
            err => {
              console.log(err);
              this.isCreatingRequest = false;
              this.requestCreationFailed = true;
            }
          );
        }
      });
    },

    refreshServiceRequests() {
      SaveServiceRequestsToStore(this.issuerID);
    }
  }
};
</script>


<style lang="sass" scoped>

</style>



