import Vue from "vue";
import Router from "vue-router";

import Login from "@/components/Login";
import Dashboard from "../components/dashboard/Dashboard";
import PageNotFound from "@/components/404";

import CreateServiceRequest from "../components/serviceRequests/CreateServiceRequest";
import ListServiceRequests from "../components/serviceRequests/ListRequests";

// Allocation routes
// import InitiateAllocation from "../components/allocation/InitiateAllocation";
import InitiateAllocation from "../components/allocation/AllocationProcess";
// import PreviewAllocationResults from "../components/allocation/PreviewAllocationResults";
// import AcceptAllocationResults from "../components/allocation/AcceptAllocationResults";
// import PublishAllocationResults from "../components/allocation/PublishAllocationResults";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/login",
      name: "Login",
      component: Login
    },
    {
      path: "/dashboard",
      name: "Dashboard",
      component: Dashboard
    },
    {
      path: "/create-service-request",
      name: "CreateServiceRequest",
      component: CreateServiceRequest
    },
    {
      path: "/service-requests",
      name: "ListServiceRequests",
      component: ListServiceRequests
    },
    {
      path: "/allocation/initiate",
      name: "initiate-allocation",
      component: InitiateAllocation
    },
    {
      path: "*",
      name: "PageNotFound",
      component: PageNotFound
    }
  ]
});



