<template>
    <div> 
        <v-dialog v-model="showViewRequestDialog" fullscreen transition="dialog-bottom-transition" :overlay=false>
          <!--Button used to trigger the opening of the view batch dialog -->
          <v-btn id="openViewRequestDialog"
          style="display: none" color="primary" dark slot="activator" ref="triggerButton">
          </v-btn>

          <v-card>
            <v-card-title class="d-flex justify-space-between bg-csp-light-blue p5 pt10">
                <h5 class="white--text font-size-20px"> SERVICE REQUEST DETAILS</h5>
                   <v-btn small class="red darken-1 white--text"
                        @click="cancelViewRequest()">
                        CLOSE
                  </v-btn>
            </v-card-title>
              <!-- Content -->
            <v-card-text>

              <!-- Batch Details -->
              <v-container v-if="request" fluid class="p0">
                <div>
                  <ul class="list-group list-group-flush black--text">
                    <li class="list-group-item font-weight-bold pt5 pb5">SUBJECT
                      <!-- Index Change -->
                      <span class="ml-auto font-weight-normal">
                        <span>{{request.subject}}</span>
                      </span>
                    </li>
                      <li class="list-group-item font-weight-bold pt5 pb5">REQUEST DESCRIPTION
                      <!-- Index Change -->
                      <span class="ml-auto font-weight-normal">
                        <span>{{request.description}}</span>
                      </span>
                    </li>
                    <li class="list-group-item font-weight-bold pt5 pb5">REQUEST STATUS
                      <!-- Index Change -->
                      <span class="ml-auto font-weight-normal">
                        <span>{{request.status_text}}</span>
                      </span>
                    </li>
                      
                  </ul>
                </div>
              </v-container>

              <!-- Batch Table -->
              <!-- <v-container v-if="batch" fluid class="p0 mt40">
                  <v-btn small class="success darken-1 white--text"
                        @click="submitBatch(batch)">
                        SUBMIT BATCH
                  </v-btn>
              </v-container> -->

              <!-- Buttons -->
              <!-- <v-container fluid class="p0">
                  <v-layout  row class="mt20">
                    <v-flex xs12 sm6>
                        <v-btn small block class="red darken-1 white--text"
                        @click="cancelViewBatch()">
                        CLOSE
                        </v-btn>
                    </v-flex>
                  </v-layout>
              </v-container> -->
            </v-card-text>
          </v-card>
      </v-dialog>

    </div>
</template>


<script>
export default {
  props: {
    request: {
      type: Object,
      default: {}
    },
    show: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      showViewRequestDialog: false
    };
  },

  methods: {
    cancelViewRequest: function() {
      // Close the dialog modal
      this.showViewRequestDialog = false;
    }
  }
};
</script>


<style lang="sass" scoped>

</style>



