<template>
  <div>
    <v-dialog v-model="showAllocationPreviewDetailsDialog" fullscreen lazy absolute persistent transition="dialog-bottom-transition">
        <!--Button used to trigger the opening of the edit user dialog -->
        <v-btn id="openAllocationPreviewDetailsDialog"
        style="display: none" color="primary" dark slot="activator" ref="triggerButton">
        </v-btn>

        <v-card>
            <v-toolbar dark class="primary">
                <v-btn icon @click.native="showAllocationPreviewDetailsDialog = false" dark>
                    <v-icon>close</v-icon>
                </v-btn>
                <v-toolbar-title class="font-size-18">Allocation Preview</v-toolbar-title>
                <v-spacer></v-spacer>
            </v-toolbar>
          <v-card-text class="p0 mt15">
            <v-layout>
              <v-flex>
                  <!-- Are you sure you want to begin the allocation process for the IPO. -->

                <v-data-table
                    hide-actionss
                    select-all="blue"
                    v-model="selected"
                    selected-key="id"
                    :items="allocationDetails"
                    :loading="loadingDataTable"
                    :rows-per-page-items="rowsPerPageItems"
                    class="elevation-1 marketDataTable table-striped table-bordered">
                    <template slot="headers" scope="props">
                    <tr class="bg-csp-light-blue marketDataTableHeader">
                        <th class="text-left">APPLICATION ID</th>
                        <th class="text-left">AGENT</th>
                        <th class="text-left">AMOUNT REQ</th>
                        <th class="text-left">AMOUNT ALLOCATED</th>
                        <th class="text-left">TOTAL AMOUNT DUE</th>
                    </tr>
                    </template>

                    <template slot="items" scope="props">
                    <tr :active="props.selected" @click="props.selected = !props.selected">
                        <td>{{ props.item.subject }}</td>
                        <td>{{ props.item.status_text }}</td>
                        <td>{{ props.item.subject }}</td>
                        <td>{{ props.item.status_text }}</td>
                        <td>{{ props.item.subject }}</td>
                    </tr>
                    </template>
                </v-data-table>

              </v-flex>
            </v-layout>

              <!-- Buttons -->
              <!-- <v-container fluid class="p0 ">
                  <v-layout  row class="mt20">
                      <v-flex>
                      <v-btn small info class=" darken-1 white--text"
                      @click="initiateAllocation()"
                      :disabled="isInitiatingAllocation" :loading="isInitiatingAllocation">
                      INITIATE  ALLOCATION
                      </v-btn>
                  </v-flex>
                  </v-layout>
              </v-container> -->
          </v-card-text>
        </v-card>
    </v-dialog>

    <!-- Snackbar to show success message when application is rejected -->
    <v-snackbar success
    :timeout="snackbarTimeout"
    :top="true"
    :color="'success'"
    v-model="allocationInitiated"
    >
    Allocation initiated
    </v-snackbar>

    <!-- Snackbar to show error message when application rejection fails -->
    <v-snackbar error
    :timeout="snackbarTimeout"
    :top="true"
    :color="'error'"
    v-model="allocationInitiationFailed"
    >
    Failed to initiate the allocation process
    </v-snackbar>
  </div>
</template>


<script>
import * as mutationTypes from "../../../store/mutation-types";
import { mapGetters } from "vuex";
export default {
  props: {
    show: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      snackbarTimeout: 4000,
      isInitiatingAllocation: false,
      allocationInitiated: false,
      allocationInitiationFailed: false,
      showAllocationPreviewDetailsDialog: false,
      valid: false,
      reasonForRejectingApplication: "",
      requiredRule: [v => !!v || "Required"],
      search: "",
      selected: [],
      loadingDataTable: false,
      rowsPerPageItems: [10, 15, 20, 25, { text: "All", value: -1 }]
    };
  },

  computed: {
    ...mapGetters({
      allocationDetails: "allocationDetails"
    })
  },

  methods: {
    initiateAllocation: function() {
      this.moveToAllocationPreview();
      this.closeInitiateAllocationDialog();
    },

    moveToAllocationPreview() {
      this.$store.commit(mutationTypes.UPDATE_ALLOCATION_FORM_STEPPER, 2);
    },

    closeInitiateAllocationDialog: function() {
      // Close the dialog modal
      this.showInitiateAllocationDialog = false;
    }
  }
};
</script>


<style lang="sass" scoped>
  .marketDataTableHeader,
  .marketDataTableBody
    height: 25px

  .marketDataTableHeader
    th
      padding: 5px 5px !important
      color: #FFFFFF !important
      font-weight: bold
      font-size: 10px

  .marketDataTableBody
    border-bottom: none !important
    &:hover
      background: #CCC !important
      cursor: pointer

  .marketDataTableBody
    td
      padding: 5px 5px !important
      height: 25px
      color: #31708f
    td:last-child
      border-bottom: 0px solid transparent !important
  .btn__content
    padding: 0px !important
  .eemulate-link
    &:hover
      transition: all .2s
      text-decorations: underline
      border-bottom: 2px solid #1a2155
      padding-bottom: 2px
      cursor: pointer
      background: #EEEEEE

  .selectedRow
    background: #b0bed9 !important
    font-weight: bold
</style>



