/**
 * Authenticate an issuing house user
 *
 * @param credentials
 */
export const ChangePassword = credentials => {
  //   return axios({
  //     url: ApiUrlService.ChangePasswordUrl,
  //     method: "post",
  //     data: credentials
  //   });

  const success = Math.random() > 0.5;

  const userData = {
    name: "Demo User",
    hasLoggedIn: true
  };

  const rejectionData = {
    message: "Password change failed"
  };

  return new Promise((resolve, reject) => {
    resolve(userData);
    if (success) {
      resolve(userData);
    } else {
      reject(rejectionData);
    }
  });
};



